.menu__list-link {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
  color: #EFF3F5;
  text-decoration: none;
  padding-bottom: 5px;
}

/* .menu__list-link:hover {
  border-bottom: 2px solid #EFF3F5;
} */

.menu__list-item {
  margin-right: 30px;
}

.menu__list-item:last-of-type {
  margin-right: 0;
}

.menu__list-link_active {
  border-bottom: 2px solid #EFF3F5;
}

.menu__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  max-width: 775px;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .menu__list {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  .menu__list-link {
    color: #6E105E;
  }

  .menu__list-item {
    margin: 30px 0 0;
  }

  .menu__list-link_active {
    border-color: #6E105E;
  }
}
